@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{overflow:hidden}*{box-sizing:border-box}.page{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}

.happy-bday-page{width:100vw;height:100vh;background-size:cover;background-color:black;margin-top:-53.2px}



.image-section{display:-webkit-flex;display:flex;-webkit-flex-flow:row wrap;flex-flow:row wrap;-webkit-justify-content:center;justify-content:center}

.image-component img{width:300px}.flip-card{background-color:transparent;width:300px;height:300px;overflow:hidden;border:1px solid #f1f1f1;-webkit-perspective:1000px;perspective:1000px}.flip-card-inner{position:relative;width:100%;height:100%;text-align:center;transition:-webkit-transform 0.8s;transition:transform 0.8s;transition:transform 0.8s, -webkit-transform 0.8s;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.flip-card:hover .flip-card-inner{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.flip-card-front,.flip-card-back{position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden}.flip-card-front{background-color:#bbb;color:black}.flip-card-back{background-color:dodgerblue;color:white;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}

.video-iframe{width:70vw;height:70vh}.wise-iframe-wrapper{position:relative;padding-bottom:56.10%;height:0;overflow:hidden}.wise-iframe-wrapper iframe,.wise-iframe-wrapper object,.wise-iframe-wrapper embed{position:absolute;top:0;left:0;width:100%;height:100%}

.nav-bar{background-color:rgba(167,167,167,0.2);padding:0px;margin:0px;height:53.2px;width:100vw;width:100vw;box-shadow:5px 5px 5px rgba(90,90,90,0.308)}.nav-bar ul{position:fixed;margin:0px}

.nav-item{display:inline-block;padding:15px 0px;font-size:1.2em;font-family:'Roboto', sans-serif}.nav-item a{color:black;text-decoration:none;width:100%;height:100%;padding:15px 20px}.nav-item:hover{background-color:orange}

